import React from "react"
import Tasks from './components/Tasks'
import Accordion from './components/Accordion'
import Weather from './asyncComponents/Weather'
import { useSelector, useDispatch } from 'react-redux'
import { showWeather } from '../redux/slices/weatherShower'

const Global = (props) => {
    const isWeatherDisplayed = useSelector((state) => {
        console.log('state', state)
        return state.weatherShower.value
    })

    const dispatch = useDispatch()

    const buttonContents = [
        'El tiempo en Espa√±a'
    ]

    const bodyContents = [
        <Weather />
    ]

    const onClickHandlers = [
        () => dispatch(showWeather()),
        () => {console.log('hola')}
    ]

    return(
        <React.Fragment>
            <button onClick={() => dispatch(showWeather())}>See how miss to 2022</button>
            <Accordion 
                label={'labelA'} 
                parentId={'parendA'} 
                target={'targetA'} 
                n={1} 
                buttonContents={buttonContents} 
                bodyContents={bodyContents} 
                isCollapsed={isWeatherDisplayed} 
                onClickHandlers={onClickHandlers}
            />
            <Tasks />
        </React.Fragment>
    )
}

export default Global;